:root {
  --red: #ff3030;
  --blue: #005eff;
  --white: #fff;
  --firework-color: var(--white);
  --firework-shadow: var(--white);
  --fw1-offset: 0px;
  --fw2-offset: 0px;
  --fw3-offset: 0px;
}

*, :before, :after {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  overflow-y: hidden;
}

body {
  color: #fff;
  background-color: #0f0f0f;
  min-height: 100vh;
  font-family: system-ui;
  display: grid;
  position: relative;
}

.user-start {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.start-button {
  font: inherit;
  color: #fff;
  cursor: pointer;
  background-color: #0f0f0f;
  border: 1px solid #fff;
  border-radius: 30px;
  padding: .5em 1em;
  font-size: clamp(1rem, 4vw, 1.3rem);
  transition: all .4s;
}

.start-button:hover {
  background-color: #212121;
}

.firework-all, .launcher-container {
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  gap: 3rem;
  display: grid;
}

.launcher {
  z-index: 1;
  opacity: 0;
  background-color: #0000;
  width: 9px;
  min-height: 9px;
  position: relative;
}

.launch-animation {
  animation: 2.6s ease-out infinite launch;
}

.launcher:after {
  z-index: -1;
  content: "";
  background-color: var(--dropShadow-color);
  filter: drop-shadow(0 0 1.2em var(--dropShadow-color));
  box-shadow: 0 1em 1em var(--boxShadow-color);
  width: 3px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 36%;
}

.lchr-1 {
  --offsetTop: var(--fw1-offset);
  --dropShadow-color: none;
  --boxShadow-color: none;
}

.lchr-2 {
  --offsetTop: var(--fw2-offset);
  --dropShadow-color: none;
  --boxShadow-color: none;
  animation-delay: .3s;
}

.lchr-3 {
  --offsetTop: var(--fw3-offset);
  --dropShadow-color: none;
  --boxShadow-color: none;
  animation-delay: .6s;
}

.firework-container {
  filter: blur(.1rem);
  width: 9px;
  min-height: 9px;
  position: relative;
}

.firework-container:first-of-type, .launcher:first-of-type {
  margin-left: 10rem;
}

.launcher:first-of-type {
  align-self: end;
}

.firework-container:nth-of-type(2), .launcher:nth-of-type(2) {
  place-self: end left;
  margin-bottom: 5rem;
  margin-left: 15rem;
}

.launcher:nth-of-type(2) {
  margin-bottom: 0;
}

.firework-container:nth-of-type(3), .launcher:nth-of-type(3) {
  place-self: start left;
  margin-top: 15rem;
  margin-left: 8rem;
}

.launcher:nth-of-type(3) {
  align-self: end;
}

.firework-spark {
  opacity: 0;
  background-color: var(--firework-color);
  filter: drop-shadow(0 0 .4em var(--firework-shadow));
  border-radius: 2px;
  width: 100%;
  min-height: 100%;
  transition: background-color .4s;
  position: absolute;
}

.firework-animation {
  animation: 2.6s ease-in infinite forwards explode;
}

.fw-2 {
  animation-delay: .3s;
}

.fw-3 {
  animation-delay: .5s;
}

@keyframes launch {
  0% {
    opacity: 1;
  }

  15% {
    opacity: 1;
  }

  20% {
    opacity: .8;
  }

  23.5%, 100% {
    opacity: 0;
    transform: translateY(calc(var(--offsetTop)  - 100vh));
  }
}

@keyframes explode {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 0;
    transform: translate(0);
  }

  30% {
    opacity: 1;
  }

  45%, 100% {
    transform: translate(var(--x-direction), var(--y-direction));
    opacity: 0;
  }

  100% {
  }
}

.firework-spark:first-of-type {
  --x-direction: calc(200px * cos(0deg));
  --y-direction: calc(-200px * sin(0deg));
}

.firework-spark:nth-of-type(2) {
  --x-direction: calc(200px * cos(30deg));
  --y-direction: calc(-200px * sin(30deg));
}

.firework-spark:nth-of-type(3) {
  --x-direction: calc(200px * cos(60deg));
  --y-direction: calc(-200px * sin(60deg));
}

.firework-spark:nth-of-type(4) {
  --x-direction: calc(200px * cos(90deg));
  --y-direction: calc(-200px * sin(90deg));
}

.firework-spark:nth-of-type(5) {
  --x-direction: calc(200px * cos(-120deg));
  --y-direction: calc(-200px * sin(120deg));
}

.firework-spark:nth-of-type(6) {
  --x-direction: calc(200px * cos(-150deg));
  --y-direction: calc(-200px * sin(150deg));
}

.firework-spark:nth-of-type(7) {
  --x-direction: calc(200px * cos(-180deg));
  --y-direction: calc(-200px * sin(180deg));
}

.firework-spark:nth-of-type(8) {
  --x-direction: calc(200px * cos(-210deg));
  --y-direction: calc(-200px * sin(210deg));
}

.firework-spark:nth-of-type(9) {
  --x-direction: calc(200px * cos(-240deg));
  --y-direction: calc(-200px * sin(240deg));
}

.firework-spark:nth-of-type(10) {
  --x-direction: calc(200px * cos(270deg));
  --y-direction: calc(-200px * sin(270deg));
}

.firework-spark:nth-of-type(11) {
  --x-direction: calc(200px * cos(300deg));
  --y-direction: calc(-200px * sin(300deg));
}

.firework-spark:nth-of-type(12) {
  --x-direction: calc(200px * cos(330deg));
  --y-direction: calc(-200px * sin(330deg));
}

@media (width <= 800px) {
  .firework-container:first-of-type, .launcher:first-of-type {
    justify-self: right;
    margin-left: 0;
  }

  .launcher:first-of-type {
    align-self: end;
  }

  .firework-container:nth-of-type(2), .launcher:nth-of-type(2) {
    place-self: end left;
    margin-bottom: 0;
    margin-left: 0;
  }

  .launcher:nth-of-type(2) {
    margin-bottom: 0;
  }

  .firework-container:nth-of-type(3), .launcher:nth-of-type(3) {
    place-self: center left;
    margin-top: 0;
    margin-left: 0;
  }

  .launcher:nth-of-type(3) {
    align-self: end;
  }
}

